import * as React from "react";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import {thumbContainer, topContainer, banner, backdrop} from "../styles/contentThumbnail.module.css"
import { useStaticQuery, graphql } from "gatsby";

const ContentThumbnail = ({title, alt, keyname}) => {
    const data = useStaticQuery(graphql`
    query ($keyname: String) {
      allFile(filter: { sourceInstanceName: { eq: "images" }, name: { eq: $keyname } }) {
        nodes {
          name
          childImageSharp {
            gatsbyImageData
          }
        }
      }
    }
  `);

    const matchingImage = data.allFile.nodes.find(node => node.name === keyname);
    const imageSrc = getImage(matchingImage);

    return(
        <div className={thumbContainer}>
            <div className={topContainer}>
                <div className={banner}>{title}</div>
                <GatsbyImage
                    image = {imageSrc}
                    alt={alt}
                    placeholder="blurred"
                    layout="constrained"
                />
            </div>
            <div className={backdrop}></div>
        </div>
    )  
}

export default ContentThumbnail;